const paint = {
  setColor: function chooseC() {
    document.querySelector('#choose').addEventListener('click', (e) => {
      document.body.style.cursor = `url(${e.target.src}), pointer`;
      document.querySelector('.color').addEventListener('change', () => {
        document.body.style.cursor = `url(${e.target.src}), pointer`;
        const chooseColor = document.querySelector('.color').value;
        document.querySelector('#choose').alt = chooseColor;
        setTimeout(() => {
          document.body.style.cursor = 'default';
        }, 1000);
      });
    });
  },
  draw: function drPixel(ctx) {
    document.querySelector('#paint').addEventListener('click', () => {
      document.querySelector('.canvas').onmousemove = function click() {
        document.querySelector('.canvas').onmousedown = function mouseClick(ev) {
          const coords = {
            x: 0,
            y: 0,
          };
          if (ev.offsetX > 10 && ev.offsetX < 60) {
            const x2 = ev.offsetX - (ev.offsetX % 10);
            coords.x = x2;
          }
          if (ev.offsetY > 10 && ev.offsetY < 60) {
            const y2 = ev.offsetY - (ev.offsetY % 10);
            coords.y = y2;
          }
          if (ev.offsetX >= 60 && ev.offsetX < 110) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 20;
            coords.x = x2;
          }
          if (ev.offsetX >= 110 && ev.offsetX < 150) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 30;
            coords.x = x2;
          }
          if (ev.offsetX >= 150 && ev.offsetX < 200) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 40;
            coords.x = x2;
          }
          if (ev.offsetX >= 200 && ev.offsetX < 250) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 50;
            coords.x = x2;
          }
          if (ev.offsetX >= 250 && ev.offsetX < 300) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 60;
            coords.x = x2;
          }
          if (ev.offsetX >= 300 && ev.offsetX < 340) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 70;
            coords.x = x2;
          }
          if (ev.offsetX >= 340 && ev.offsetX < 420) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 80;
            coords.x = x2;
          }
          if (ev.offsetY >= 60 && ev.offsetY < 110) {
            const y2 = ev.offsetY - (ev.offsetY % 10) - 20;
            coords.y = y2;
          }
          if (ev.offsetY >= 110 && ev.offsetY < 160) {
            const y2 = ev.offsetY - (ev.offsetY % 10) - 30;
            coords.y = y2;
          }
          if (ev.offsetY >= 160 && ev.offsetY < 190) {
            const y2 = ev.offsetY - (ev.offsetY % 10) - 40;
            coords.y = y2;
          }
          ctx.beginPath();
          ctx.rect(coords.x, coords.y, 10, 10);
          ctx.strokeStyle = '#49423d';
          ctx.lineWidth = '1';
          ctx.fillStyle = document.querySelector('#choose').alt;
          ctx.stroke();
          ctx.fill();
        };
      };
      document.addEventListener('keypress', (event) => {
        if (event.keyCode === 13) {
          const tmp = document.querySelector('.c-1');
          const img = document.getElementById('img');
          const ref = document.getElementById('save-img');
          img.src = tmp.toDataURL();
          const imgdata = tmp.toDataURL('image/png');
          const newdata = imgdata.replace(/^data:image\/png/, 'data:application/octet-stream');
          console.log(newdata);
          const slide = document.querySelectorAll('.slide');
          const imgSlide = document.createElement('img');
          const way = tmp.toDataURL();
          imgSlide.src = way;
          ref.href = way;
          imgSlide.style.display = 'inline';
          imgSlide.style.width = '170px';
          slide[slide.length - 1].appendChild(imgSlide);
          img.style.display = 'none';
        }
      });
    });
  },
  erase: function erasePx(ctx) {
    function erasePixel() {
      document.querySelector('.canvas').onmousemove = function click() {
        document.querySelector('.canvas').onmousedown = function mouseClick(ev) {
          const coords = {
            x: 0,
            y: 0,
          };
          if (ev.offsetX > 10 && ev.offsetX < 60) {
            const x2 = ev.offsetX - (ev.offsetX % 10);
            coords.x = x2;
          }
          if (ev.offsetY > 10 && ev.offsetY < 60) {
            const y2 = ev.offsetY - (ev.offsetY % 10);
            coords.y = y2;
          }
          if (ev.offsetX >= 60 && ev.offsetX < 110) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 20;
            coords.x = x2;
          }
          if (ev.offsetX >= 110 && ev.offsetX < 150) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 30;
            coords.x = x2;
          }
          if (ev.offsetX >= 150 && ev.offsetX < 200) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 40;
            coords.x = x2;
          }
          if (ev.offsetX >= 200 && ev.offsetX < 250) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 50;
            coords.x = x2;
          }
          if (ev.offsetX >= 250 && ev.offsetX < 300) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 60;
            coords.x = x2;
          }
          if (ev.offsetX >= 300 && ev.offsetX < 340) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 70;
            coords.x = x2;
          }
          if (ev.offsetX >= 340 && ev.offsetX < 420) {
            const x2 = ev.offsetX - (ev.offsetX % 10) - 80;
            coords.x = x2;
          }
          if (ev.offsetY >= 60 && ev.offsetY < 110) {
            const y2 = ev.offsetY - (ev.offsetY % 10) - 20;
            coords.y = y2;
          }
          if (ev.offsetY >= 110 && ev.offsetY < 160) {
            const y2 = ev.offsetY - (ev.offsetY % 10) - 30;
            coords.y = y2;
          }
          if (ev.offsetY >= 160 && ev.offsetY < 190) {
            const y2 = ev.offsetY - (ev.offsetY % 10) - 40;
            coords.y = y2;
          }
          ctx.beginPath();
          ctx.rect(coords.x, coords.y, 10, 10);
          ctx.strokeStyle = '#49423d';
          ctx.lineWidth = '1';
          ctx.fillStyle = '#bbbbbb';
          ctx.stroke();
          ctx.fill();
        };
      };
    }
    document.querySelector('#erase').addEventListener('click', erasePixel);
  },
};
export default paint;
